:root {
  --blue: #1da1f2;
  --black: #14171a;
  --darkgrey: #657786;
  --mediumgrey: #aab8c3;
  --lightgrey: #e1e8ed;
  --light: #f5f8fa;
  --shadow: 1px 1px 5px 1px var(--darkgrey);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  background-color: var(--black);
  transition: transform 0.2s ease-in;
}
a {
  text-decoration: none;
  color: var(--light);
}
.layout {
  width: 100%;
  max-width: 1200px;
  height: 99vh;
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  box-shadow: var(--shadow);
  border-radius: 20px;
}

.left {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  text-transform: capitalize;
  background-color: var(--black);
  color: var(--light);
  font-weight: normal;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  transform: translateX(0);
}
.left img,
.left h3 {
  padding: 10px;
}
.left h3:hover {
  color: var(--blue);
  cursor: pointer;
}
.btn {
  padding: 10px 20px;
  border: none;
  background-color: var(--blue);
  color: var(--light);
  border-radius: 20px;
  opacity: 0.9;
}
.btn:hover {
  cursor: pointer;
  opacity: 1;
}
main {
  border: 1px solid var(--blue);
  border-top: 0;
  border-bottom: 0;
  text-transform: capitalize;
  color: var(--light);
  text-transform: capitalize;
}
.addpost {
  text-align: center;
}
.addpost h3 {
  padding: 10px;
  border-bottom: 1px solid var(--blue);
}
.addpostdiv {
  display: grid;
  grid-template-columns: 1fr 8fr;
  padding: 10px;
  gap: 10px;
}
.addpost div img {
  width: 50px;
  height: 50px;
  box-shadow: var(--shadow);
  border-radius: 50px;
}
textarea:focus {
  border: none;
}
.addpost textarea {
  border: none;
  padding: 5px;
  font-size: 1rem;
  resize: none;
  height: 100px;
  color: var(--blue);
  box-shadow: var(--shadow);
  border-radius: 10px;
}
.addpost .btn {
  grid-column: 1/3;
  width: 100px;
  margin: auto;
}
.underline {
  width: 50%;
  height: 3px;
  background-color: var(--blue);
  margin: auto;
  border-radius: 10px;
}

.posts {
  overflow-y: scroll;
  height: 64%;
  max-height: 380px;
}

.posts article {
  display: flex;
  padding: 20px 10px;
  gap: 10px;
}
.liked {
  color: rgb(214, 20, 127);
}
.liked:hover {
  transform: scale(1.2);
}
.like:hover {
  cursor: pointer;
  transform: scale(1.2);
}
.postunderline {
  height: 2px;
  width: 90%;
  margin: auto;
  background-color: var(--blue);
}
.smallunderline {
  height: 2px;
  width: 50%;
  margin: 5px auto;
  background-color: var(--blue);
}
.xsunderline {
  height: 2px;
  width: 20%;
  margin: 5px auto;
  background-color: var(--blue);
}

.posts article img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  box-shadow: var(--shadow);
}
.content {
  width: 100%;
}
.profilename {
  display: flex;
  gap: 10px;
  align-items: center;
}
.profilename h5 {
  font-size: 1rem;
}

.profilename p {
  font-size: 0.8rem;
  opacity: 0.8;
}
.text {
  display: flex;
  width: 100%;
  justify-content: space-between;
  padding-right: 10px;
}

.profile {
  width: 100%;
  height: 600px;
  margin: 10px auto;
  display: grid;
  grid-template-rows: 1fr 3fr;
  justify-content: center;
  align-items: flex-start;
  border: 1px solid var(--blue);
  border-top: 0;
  border-bottom: 0;
}

.profileimg {
  align-self: flex-end;
  padding-bottom: 10px;
  border-bottom: 3px solid var(--blue);
  margin: auto;
}
.profileimg img {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  box-shadow: 1px 1px 10px 1px var(--blue);
}
.info {
  align-self: flex-start;
  text-align: center;
  color: var(--light);
  padding: 10px;
  margin: 10px;
  gap: 10px;
}
.info h2,
.info h4,
.info p {
  margin: 10px;
}
.skills {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.skills img {
  width: 50px;
  height: 50px;
}
.skills img:hover {
  background-color: var(--blue);
  box-shadow: var(--shadow);
  transform: scale(1.2);
}
.skills div {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-content: center;
  align-items: center;
  justify-items: center;
  gap: 10px;
}
.react {
  grid-column: -5/-1;
  animation: rotate 10s infinite linear;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.right {
  padding: 10px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.right input {
  justify-self: flex-start;
  padding: 10px;
  border: none;
  border-radius: 20px;
  width: 80%;
  box-shadow: 1px 1px 5px 1px var(--blue);
  background-color: var(--lightgrey);
}
.today {
  height: 100%;
  max-height: 540px;
  width: 90%;
  border-radius: 20px;
  box-shadow: var(--shadow);
}
.today div {
  overflow-y: scroll;
  height: 90%;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.today h3 {
  font-weight: normal;
  padding: 10px;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  color: var(--light);
  border-bottom: 1px solid var(--blue);
}
.today h4 {
  text-align: center;
  padding: 10px;
  color: var(--light);
}

.today article {
  padding: 5px;
  margin: 5px;
  box-shadow: var(--shadow);
}

.year {
  color: var(--blue);
  text-align: center;
}
.special {
  color: var(--darkgrey);
}

.today div::-webkit-scrollbar,
.posts::-webkit-scrollbar {
  width: 5px;
  height: 400px;
  margin-right: 10px;
}

.today div::-webkit-scrollbar-track,
.posts::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px var(--black);
  border-radius: 10px;
  margin: 20px;
}

.today div::-webkit-scrollbar-thumb,
.posts::-webkit-scrollbar-thumb {
  background: var(--blue);
  border-radius: 10px;
  opacity: 0.4;
}

.today div::-webkit-scrollbar-thumb:hover,
.posts::-webkit-scrollbar-thumb:hover {
  background: var(--darkgrey);
}
.lines,
.left p {
  display: none;
}
.left p:hover {
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .right {
    display: none;
  }
  .layout {
    grid-template-columns: 1fr 3fr;
    border-radius: 0;
  }
  .lines {
    display: none;
  }
}
@media screen and (min-width: 768px) {
  .left {
    transform: translateX(0) !important;
  }
}
@media screen and (min-width: 521px) {
  .left {
    transform: translateX(0) !important;
  }
}

@media screen and (max-width: 520px) {
  .layout {
    grid-template-columns: 1fr;
    height: 100%;
    border-radius: 0;
  }

  .left {
    position: fixed;
    width: 50%;
    height: 100%;
    transform: translateX(-230px);
    top: 0;
    left: 0;
    background-color: var(--black);
    opacity: 0.8;
  }
  .left p {
    display: block;
    align-self: flex-end;
    padding: 10px;
    font-weight: bold;
    color: red;
  }

  .title {
    display: flex;
    justify-content: space-between;
    padding: 0 10px;
    align-items: center;
    border-bottom: 1px solid var(--blue);
  }
  .title h3 {
    border-bottom: 0;
  }
  .lines {
    display: block;
    width: 40px;
  }
  .profilelines {
    width: 60px;

    padding: 10px;
  }
  .lines div {
    width: 50%;
    height: 2px;
    margin: 2px;
    background-color: var(--blue);
  }
  .lines:hover {
    transform: scale(1.1);
    opacity: 0.8;
  }
}
